@charset "UTF-8";
body {
    font-family: "Arial";
    font-size: 12px;
    font-weight: 400;
    color: #b2b2b2;
}

h1 {
    font-size: 90px;
    font-family: "roadwayregular";
    color: #fff;
}

h2 {
    font-family: "_officina_sans";
    text-transform: uppercase;
    transition: color 0.5s ease 0s;
}
h2:hover {
    opacity: 0.8;
}

h3 {
    font-family: "moonlight";
    font-size: 16px;
    color: #000;
    margin-bottom: 20px;
}

h4 {
    font-family: "roadwayregular";
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
}

h5 {
    font-family: "roadwayregular";
    font-size: 16px;
    color: #fff;
}

h6 {
    font-family: "moonlight";
    font-size: 16px;
    color: #649671;
}

a {
    color: #fff;
}

@font-face {
    font-family: "roadwayregular";
    src: url("../fonts/roadway_-webfont.eot");
    src: url("../fonts/roadway_-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/roadway_-webfont.woff2") format("woff2"), url("../fonts/roadway_-webfont.woff") format("woff"), url("../fonts/roadway_-webfont.ttf") format("truetype"), url("../fonts/roadway_-webfont.svg#roadwayregular") format("svg");
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: "moonlight";
    src: url("../fonts/moon_light-webfont.eot");
    src: url("../fonts/moon_light-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/moon_light-webfont.woff2") format("woff2"), url("../fonts/moon_light-webfont.woff") format("woff"), url("../fonts/moon_light-webfont.ttf") format("truetype"), url("../fonts/moon_light-webfont.svg#moonlight") format("svg");
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: "_officina_sans";
    src: url("../fonts/itc-officina-sans-std-book-font-webfont.eot");
    src: url("../fonts/itc-officina-sans-std-book-font-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/itc-officina-sans-std-book-font-webfont.woff2") format("woff2"), url("../fonts/itc-officina-sans-std-book-font-webfont.woff") format("woff"), url("../fonts/itc-officina-sans-std-book-font-webfont.ttf") format("truetype"), url("../fonts/itc-officina-sans-std-book-font-webfont.svg#itc_officina_sans_stdbook") format("svg");
    font-weight: normal;
    font-style: normal;
}
html, body {
    height: 100%;
    font-size: 12px;
    font-family: "Arial";
    background: url(../img/background.jpg);
}

.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.content {
    flex: 1 1 auto;
}

[class*=__container] {
    max-width: 1020px;
    margin: 0 auto;
}

.header__container {
    padding: 23px 0;
}

.header__top--list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
}
.header__top--item {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 5px 10px;
    border-radius: 2px;
    transition: background 0.5s ease 0s;
}
.header__top--item:hover {
    background-color: rgba(10, 9, 9, 0.55);
}
.header__top--link {
    color: #fff;
    font-size: 12px;
}
.header__top--img {
    width: 16px;
    height: 11px;
}

.header__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #000;
    padding: 0 10px;
    margin-top: 25px;
}

.logo__link {
    font-size: 36px;
    text-transform: uppercase;
}

.nav__list {
    display: flex;
    gap: 35px;
}
.nav__link {
    color: #b2b2b2;
    font-family: "roadwayregular";
    font-size: 20px;
    transition: color 0.5s ease 0s;
}
.nav__link:hover {
    color: #fff;
}

.social__list {
    display: flex;
    align-items: center;
    gap: 10px;
}
.social__link {
    font-size: 14px;
    font-family: "roadwayregular";
    transition: color 0.5s ease 0s;
}
.social__link:hover {
    opacity: 0.8;
}

.main__container {
    padding: 0;
}

.main__content {
    display: flex;
    justify-content: space-between;
    text-align: center;
    background-color: #fff;
    padding: 25px 40px 80px 25px;
}

.slider__holder {
    position: relative;
}
.slider__content {
    display: inline-flex;
    flex-direction: column;
    padding: 130px 0 0 60px;
}
.slider__content--title {
    padding: 10px;
    background-color: rgba(31, 31, 31, 0.9);
}
.slider__content--text {
    width: 320px;
    font-size: 30px;
    color: #b2b2b2;
    background-color: rgba(31, 31, 31, 0.9);
    padding: 10px;
    text-transform: uppercase;
    font-family: "roadwayregular";
}
.slider__item {
    height: 350px;
}
.slider__controls {
    position: absolute;
    top: 0;
    left: 62%;
}
.slider__control {
    background-color: rgba(31, 31, 31, 0.9);
    padding: 10px;
    transition: background 0.5s ease 0s;
}
.slider__control:hover {
    background-color: #fff;
    color: #000;
}

.control__content--title {
    color: #fff;
    transition: background 0.5s ease 0s;
}
.control__content--title:hover {
    color: #000;
}

.aside {
    display: flex;
    flex-direction: column;
    text-align: start;
    background-color: #e6f5ea;
    padding: 15px 15px;
}

.aside-1__img {
    width: 160px;
    height: 125px;
}
.aside-1__subtitle {
    margin: 20px 0;
    color: #649671;
}
.aside-1__text {
    width: 150px;
    border-bottom: 1px solid #b2b2b2;
    padding-bottom: 15px;
    margin-bottom: 15px;
}
.aside-1__link {
    color: #649671;
}
.aside-1__link:hover {
    text-decoration: underline;
}

.border {
    width: 160px;
    padding: 0 0 15px 0;
    border-bottom: 1px solid #649671;
}

.center {
    display: flex;
    flex-direction: column;
    text-align: start;
    width: 49.5%;
    padding-top: 20px;
}
.center__bigimg {
    max-width: 100%;
}
.center__subtitle {
    position: relative;
    padding: 5px;
    background-color: rgba(31, 31, 31, 0.9);
    top: -30px;
}

.green {
    display: flex;
    flex-direction: column;
    text-align: start;
}
.green__title {
    padding: 5px 10px;
    background-color: #94bc9e;
    margin: 14px 0 30px;
}
.green__link {
    margin-top: 25px;
}
.green__link--link {
    color: #649671;
}
.green__link--link:hover {
    text-decoration: underline;
}

.aside-2 {
    background-color: #fff;
}

.subscrube__list {
    display: flex;
    gap: 10px;
    margin-top: 10px;
}
.subscrube__link {
    transition: color 0.5s ease 0s;
}
.subscrube__link:hover {
    opacity: 0.8;
}

.vote {
    background-color: #e6f5ea;
    margin-top: 35px;
    padding: 15px;
}
.vote__text {
    color: #000;
    font-weight: 700;
    margin-bottom: 15px;
}

.popular {
    margin: 40px 0;
}
.popular__item:not(:last-child) {
    border-bottom: 1px solid #94bc9e;
    padding-bottom: 10px;
    margin-bottom: 10px;
}
.popular__link {
    color: #649671;
    padding-left: 10px;
}
.popular__link:hover {
    text-decoration: underline;
}

.twitter__twit {
    background-color: #e6f5ea;
    margin-top: 30px;
    padding: 15px;
}
.twitter__twit--link {
    color: #94bc9e;
}
.twitter__twit--link:hover {
    text-decoration: underline;
}

.footer__container {
    padding: 50px;
    background-color: rgba(31, 31, 31, 0.9);
}
.footer__top {
    display: flex;
    align-items: center;
}

.small {
    display: flex;
    margin-left: -10px;
}
.small__img {
    padding-left: 10px;
}

.footer__logo--link {
    font-size: 26px;
}

.menu__footer {
    padding-left: 50px;
}
.menu__footer--list {
    display: flex;
    gap: 25px;
}
.menu__footer--link {
    color: #b2b2b2;
    font-family: "roadwayregular";
    font-size: 16px;
    transition: color 0.5s ease 0s;
}
.menu__footer--link:hover {
    color: #fff;
}

.buttom__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
}
.buttom__footer--text {
    width: 145px;
    font-size: 12px;
    font-style: italic;
    text-align: end;
    line-height: 1.3;
}
.buttom__footer--title {
    display: inline-block;
    background-color: #000;
    padding: 8px;
}
.buttom__footer--subtitle {
    width: 320px;
    font-size: 12px;
    background-color: #000;
    padding: 10px;
}
.buttom__footer--list {
    display: flex;
    gap: 15px;
    background-color: #000;
    padding: 13px 10px;
    align-items: self-end;
}
.buttom__footer--item {
    font-size: 12px;
}
.buttom__footer--link {
    transition: color 0.5s ease 0s;
}
.buttom__footer--link:hover {
    opacity: 0.8;
}

@media (max-width: 960px) {
    .logo__link {
        font-size: 30px;
    }
    .nav__link {
        font-size: 16px;
    }
    .footer__top {
        flex-direction: column;
    }
    .footer__logo {
        margin-bottom: 20px;
    }
    .buttom__footer {
        flex-direction: column;
        text-align: center;
    }
    .buttom__footer--text {
        text-align: center;
        margin: 0 auto;
    }
    .buttom__footer--about {
        margin: 20px 0;
    }
    .buttom__footer--subtitle, .buttom__footer--subscribe {
        margin: 0 auto;
    }
}
@media (max-width: 768px) {
    header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background-color: #000;
    }
    .hamburger {
        z-index: 100;
        position: relative;
        flex: 0 0 60px;
        height: 40px;
        width: 60px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        left: 85%;
        top: 70px;
    }
    .hamburger::before, .hamburger::after {
        content: "";
        background-color: #fff;
        height: 4px;
    }
    .hamburger span {
        height: 4px;
        background-color: #fff;
    }
    .nav {
        z-index: 50;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
    }
    .nav__list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        transform: translateX(-50%);
        left: 87%;
        top: 100%;
        opacity: 0;
        background-color: #000;
        visibility: hidden;
        margin-top: 90px;
        width: 200px;
        padding: 25px;
        transition: opacity 0.3s ease, visibility 0.3s ease;
    }
    .nav__list.active {
        visibility: visible;
        opacity: 1;
    }
    .nav__item {
        margin-bottom: 40px;
    }
    .nav__item:last-child {
        margin-bottom: 0;
    }
    .header__social {
        display: none;
    }
}
